html {
  box-sizing: border-box;
}

*, :before, :after {
  box-sizing: inherit;
}

body {
  font-family: Montserrat, sans-serif;
  font-weight: 400;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 480px) {
  .container {
    width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
    padding-left: 16px;
    padding-right: 16px;
  }
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.link {
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6, p, a, li, ul {
  margin: 0;
  padding: 0;
}

.title {
  letter-spacing: .04em;
  text-transform: uppercase;
  text-align: center;
  font-family: \$titleFontFamily;
  font-size: 30px;
  font-weight: 400;
  line-height: 1.43333;
}

@media screen and (min-width: 768px) {
  .title {
    letter-spacing: .06em;
    font-size: 34px;
    line-height: 1.26471;
  }
}

@media screen and (min-width: 1200px) {
  .title {
    font-size: 36px;
    line-height: 1.25;
  }
}

.pretitle {
  text-align: center;
